$backgroundColor: #f5f5f5;

html, body {
  height: 100%;
  margin:0;
  padding:0;
}

body {
  background: $backgroundColor;
  height: 100%;

  #root {
    display:flex;
    flex-direction:column;
    height: 100%;
  }

  footer {
    background: darken($backgroundColor, 50);
    border-top: 1px solid #eee;
    padding: 1em 3em;
    margin: 0;
  }

  header {
    background: darken($backgroundColor, 75);
    border-bottom: 1px solid #eee;
    color: #eee;
    text-align: center;
  }
  
  .object-key-val{
    width: 200px;
  }
}

li{
  display: inline-block;
  margin-right: 20px;
}

a{
  color: #eee;
  text-decoration: none;
  
}
a:hover{
  background-color: gray;
}


